export default {
    bind(el, binding){
        el.__HandleScrollHandler__ = event => binding.value(event)
        document.body.addEventListener('mousewheel', el.__HandleScrollHandler__)
        document.body.addEventListener('touchmove', el.__HandleScrollHandler__) 
    },
    
    unbind(el){
        document.body.addEventListener('mousewheel', el.__HandleScrollHandler__)
        document.body.addEventListener('touchmove', el.__HandleScrollHandler__)
    }
}


